import React, { useRef, useState, useEffect } from 'react';
import { useSpring, a } from '@react-spring/web';
import useMeasure from 'react-use-measure';
import { Container, Title, Frame, Content, toggle } from './styles';
import * as Icons from './icons';
import { ExifRow } from 'types';
import KakaoMap from 'components/IceContainer/ExifAnalyzer/KakaoMap/KakaoMap';
import { isValidLocation } from 'utils/getAddress';

function usePrevious<T>(value: T) {
  const ref = useRef<T>();
  useEffect(() => void (ref.current = value), [value]);
  return ref.current;
}

const Tree = React.memo<
  React.HTMLAttributes<HTMLDivElement> & {
    defaultOpen?: boolean;
    name: string | JSX.Element;
  }
>(({ children, name, style, defaultOpen = false }) => {
  const [isOpen, setOpen] = useState(defaultOpen);
  const previous = usePrevious(isOpen);
  const [ref, { height: viewHeight }] = useMeasure();
  const { height, opacity, y } = useSpring({
    from: { height: 0, opacity: 0, y: 0 },
    to: {
      height: isOpen ? viewHeight : 0,
      opacity: isOpen ? 1 : 0,
      y: isOpen ? 0 : 20,
    },
  });
  // @ts-ignore
  const Icon =
    Icons[`${children ? (isOpen ? 'Minus' : 'Plus') : 'Close'}SquareO`];
  return (
    <Frame>
      <Icon
        style={{ ...toggle, opacity: children ? 1 : 0.3 }}
        // style={{ ...toggle }}
        onClick={() => setOpen(!isOpen)}
      />
      <Title style={style}>{name}</Title>
      <Content
        style={{
          opacity,
          height: isOpen && previous === isOpen ? 'auto' : height,
        }}
      >
        <a.div ref={ref} style={{ y }} children={children} />
      </Content>
    </Frame>
  );
});

interface Props {
  rows: ExifRow[];
}

// EXIF  
export default function ExifDataTree({ rows }: Props) {
  return (
    <>
      <Container>
        {rows.length > 0 ? (
          <Tree name=" EXIF " defaultOpen>
            {rows
              .filter((item) => item.data != '')
              .map((item, index) => {
                const {
                  meta, //  
                  name, //  
                  data, // 
                  origindata, //  
                  type, // 
                  unit, // 
                  comment, // 
                  example, // 
                } = item;

                return (
                  <Tree
                    key={index}
                    name={name}
                    style={{ color: 'var(--container-highlight-color)' }}
                    defaultOpen
                  >
                    {/*     */}
                    {meta == 'Location' &&
                      isValidLocation(
                        origindata.split(',')[0].trim(),
                        origindata.split(',')[1].trim()
                      ) && (
                        <>
                          <KakaoMap
                            latitude={origindata.split(',')[0].trim()}
                            longitude={origindata.split(',')[1].trim()}
                          />
                        </>
                      )}
                    <Tree key={`${index}-data`} name={data}>
                      <Tree
                        key={`${index}-meta`}
                        name={'[] ' + meta}
                      ></Tree>
                      <Tree
                        key={`${index}-comment`}
                        name={'[] ' + comment}
                      ></Tree>
                      <Tree
                        key={`${index}-type`}
                        name={'[] ' + (type || '')}
                      ></Tree>
                      <Tree
                        key={`${index}-unit`}
                        name={'[] ' + (unit || '')}
                      ></Tree>
                      <Tree
                        key={`${index}-origindata`}
                        name={'[] ' + origindata}
                      ></Tree>
                      {example && (
                        <Tree
                          key={`${index}-example`}
                          name="[]"
                          defaultOpen
                        >
                          {Object.keys(example).map((key, index) => (
                            <Tree
                              key={`${index}-example-${index}`}
                              name={key + ' = ' + example[key]}
                            ></Tree>
                          ))}
                        </Tree>
                      )}
                    </Tree>
                  </Tree>
                );
              })}
          </Tree>
        ) : (
          <>
            <Tree
              name=" EXIF    "
              defaultOpen
            ></Tree>
          </>
        )}
      </Container>
    </>
  );
}
