import ExifDataTree from './ExifDataTree/ExifDataTree';
import React, { useEffect, useState } from 'react';
import { ExifRow } from 'types';
import ImgDropzone from './ImgDropzone/ImgDropzone';
import { IceFrame } from 'components/IceContainer/styles';
import { getDate } from 'utils/getDate';
import { getBytes } from 'utils/getBytes';
import { IceExifInfo, IceImageInfo } from './styles';
import { IceStatistic } from 'components/common/IceStatistic/styles';

//  
export default function ExifAnalyzer() {
  //  
  const [file, setFile] = useState<File | undefined>(undefined);
  // EXIF   
  const [rows, setRows] = useState<ExifRow[]>([]);

  // GO    
  useEffect(() => {
    const loadWebAssembly = async () => {
      const go = new Go();
      const wasmModule = await WebAssembly.instantiateStreaming(
        fetch('wasm/main.wasm'),
        go.importObject
      );
      go.run(wasmModule.instance);
    };

    loadWebAssembly();
  }, []);

  return (
    <>
      <IceFrame>
        {/*  */}
        <ImgDropzone setFile={setFile} setRows={setRows} />
        {/*     */}
        {file && (
          <>
            <IceExifInfo>
              {/*   */}
              <IceImageInfo>
                <IceStatistic title=" " value={file.name} />
                <IceStatistic
                  title=""
                  value={getDate(file.lastModified)}
                />
                <IceStatistic title=" " value={getBytes(file.size)} />
              </IceImageInfo>
              {/*  EXIF   */}
              <ExifDataTree rows={rows} />
            </IceExifInfo>
          </>
        )}
      </IceFrame>
    </>
  );
}
