import Logo from 'components/common/Logo/Logo';
import { useCallback, useState } from 'react';
import { useDropzone } from 'react-dropzone';
import { ExifRow } from 'types';
import { getAddress, isValidLocation } from 'utils/getAddress';
import { IceDropzone, StyledImage } from './styles';

interface Props {
  setFile: React.Dispatch<React.SetStateAction<File | undefined>>;
  setRows: React.Dispatch<React.SetStateAction<ExifRow[]>>;
}

export default function ImgDropzone({ setFile, setRows }: Props) {
  // 
  const [thumbnail, setThumbnail] = useState('');

  /***  &   */
  const onDrop = async (acceptedFiles: File[]) => {
    // 0 
    const inputImage = acceptedFiles[0];
    //  URL 
    setThumbnail(URL.createObjectURL(inputImage)); // 

    //   
    const binaryData = await inputImage.arrayBuffer();
    let input_data = new Uint8Array(binaryData);
    // GO    
    const result = await window.goFunc(input_data);

    //   
    setFile(inputImage);

    //   
    if (!result) {
      setRows([]);
      return;
    }

    //   
    const meta: [
      {
        tag: string; //  
        comment: string; // 
        data: string; // 
        origindata: string; //  
        type: string; // 
        name: string; //  
        unit: string; // 
        example: any; // 
      }
    ] = JSON.parse(result);

    setRows(
      await Promise.all(
        meta.map(
          async (
            { tag, comment, data, origindata, name, type, unit, example },
            index
          ) => {
            if (tag === 'Location') {
              // (, )    
              try {
                const [lat, lng] = origindata
                  .split(',')
                  .map((value) => value.trim());
                if (isValidLocation(lat, lng)) {
                  // API  
                  const address = await getAddress(lat, lng);
                  //  
                  data = address;
                }
              } catch (error_msg) {
                console.log(error_msg);
              }
            }
            return {
              id: index + 1,
              meta: tag,
              comment: comment,
              data: data,
              origindata: origindata,
              name: name,
              type: type,
              unit: unit,
              example,
            };
          }
        )
      )
    );
  };

  //   
  const { getRootProps, getInputProps, isDragActive } = useDropzone({
    onDrop,
    accept: {
      'image/jpeg': [],
      'image/png': [],
    },
  });

  // const stopPropagation = useCallback((event: any) => {
  //   event.stopPropagation();
  // }, []);

  return (
    <>
      <IceDropzone {...getRootProps()}>
        {thumbnail ? (
          <StyledImage
            src={thumbnail}
            // onClick={stopPropagation}
            preview={false}
          />
        ) : (
          <div>
            <Logo />
            <br />
            {isDragActive ? (
              <div>  !</div>
            ) : (
              <div>      !</div>
            )}
          </div>
        )}
        <input {...getInputProps()} />
      </IceDropzone>
    </>
  );
}
